import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './app';
import { BrowserRouter } from 'react-router-dom';
import NavBar from './navbar/navbar';
ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter >
      <NavBar/>
      <App/>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);
